import React from 'react'
import {graphql,useStaticQuery} from 'gatsby'
import SidebarSearch from '../../../../Global/App/SidebarWidgets/SidebarSearch.component'
import BreadcrumContent from '../../../../Global/Pages/Blog/BreadcrumbContent.component'

import BlogTagWidget from '../../../../Global/Pages/Blog/BlogTagWidget.component'
import CategoriesProperty from '../../../../Global/Pages/CategoriesProperty.component'
import SidebarFeatured from '../../../../Global/Pages/SideBarFeaturedListing.component'
import Pagination from '../../../../Global/Pages/Pagination.component'
import BlogFeatProperty from '../../../../Global/Pages/Blog/BlogFeatProperty.component'

const BlogGridSection=()=>{
	const data =useStaticQuery(graphql`
	query{
		 blog1: file(relativePath: {eq: "blog/1.jpg"}) {
			id
			childImageSharp {
			fluid {
				originalImg
			}
			}
		}
		 blog2: file(relativePath: {eq: "blog/2.jpg"}) {
			id
			childImageSharp {
			fluid {
				originalImg
			}
			}
		}
		 blog3: file(relativePath: {eq: "blog/3.jpg"}) {
			id
			childImageSharp {
			fluid {
				originalImg
			}
			}
        }
        blog4: file(relativePath: {eq: "blog/4.jpg"}) {
			id
			childImageSharp {
			fluid {
				originalImg
			}
			}
        }
        blog5: file(relativePath: {eq: "blog/5.jpg"}) {
			id
			childImageSharp {
			fluid {
				originalImg
			}
			}
        }
        blog6: file(relativePath: {eq: "blog/6.jpg"}) {
			id
			childImageSharp {
			fluid {
				originalImg
			}
			}
		}
    }`)
    return(
        <section className="blog_post_container bgc-f7">
		<div className="container">
			<div className="row">
				<div className="col-xl-6">
					<BreadcrumContent title="Blog" active="Simple Listing – Grid View"/>
				</div>
			</div>
			<div className="row">
				<div className="col-lg-8">
					<div className="row">
						<div className="col-lg-6">
							<BlogFeatProperty 
                            title="Redfin Ranks the Most Competitive Neighborhoods of 2020" 
                            details="Lorem ipsum dolor sit amet, consectetur text link libero tempus congue."
                            >
                            <img className="img-whp" src={data.blog1.childImageSharp.fluid.originalImg} alt="7.jpg"/>
                        </BlogFeatProperty>
						</div>
						<div className="col-lg-6">
							<BlogFeatProperty 
                            title="Housing Markets That Changed the Most This Decade" 
                            details="Lorem ipsum dolor sit amet, consectetur text link libero tempus congue."
                            >
                            <img className="img-whp" src={data.blog2.childImageSharp.fluid.originalImg} alt="7.jpg"/>
                        </BlogFeatProperty>
						</div>
						<div className="col-lg-6">
							<BlogFeatProperty 
                            title="Redfin Unveils the Best Canadian Cities for Biking" 
                            details="Lorem ipsum dolor sit amet, consectetur text link libero tempus congue."
                            >
                            <img className="img-whp" src={data.blog3.childImageSharp.fluid.originalImg} alt="7.jpg"/>
                        </BlogFeatProperty>
						</div>
                        <div className="col-lg-6">
							<BlogFeatProperty 
                            title="You Can Buy The Piano Teacher’s Home from Groundhog Day" 
                            details="Lorem ipsum dolor sit amet, consectetur text link libero tempus congue."
                            >
                            <img className="img-whp" src={data.blog4.childImageSharp.fluid.originalImg} alt="7.jpg"/>
                        </BlogFeatProperty>
						</div>
                        <div className="col-lg-6">
							<BlogFeatProperty 
                            title="Why We Should All Support Clear Cooperation" 
                            details="Lorem ipsum dolor sit amet, consectetur text link libero tempus congue."
                            >
                            <img className="img-whp" src={data.blog5.childImageSharp.fluid.originalImg} alt="7.jpg"/>
                        </BlogFeatProperty>
						</div>
                        <div className="col-lg-6">
							<BlogFeatProperty 
                            title="Housing Markets That Changed the Most This Decade" 
                            details="Lorem ipsum dolor sit amet, consectetur text link libero tempus congue."
                            >
                            <img className="img-whp" src={data.blog6.childImageSharp.fluid.originalImg} alt="7.jpg"/>
                        </BlogFeatProperty>
						</div>	
					</div>
                    <div className="row">
                        <div className="col-lg-12">
                            <Pagination/>
                        </div>
                    </div>
					</div>
                    <div className="col-lg-4 col-xl-4">
                        <SidebarSearch/>
                        <CategoriesProperty/>
                        <SidebarFeatured HeaderTitle="Featured Listing"/>
                        <BlogTagWidget/>
				    </div>
				</div>	
			</div>
	</section>
    )
}
export default BlogGridSection